"use client";

import type React from "react";

import { useState, useRef, useEffect } from "react";
import {
  ChevronDown,
  ChevronRight,
  Copy,
  Download,
  FileIcon,
} from "lucide-react";
import { INITIAL_FILES } from "@/components/sample";
import { toast } from "sonner";
import type { FileSystemNode } from "@/types/filesystem";

export default function CodeEditor() {
  const [files] = useState<FileSystemNode>(INITIAL_FILES);
  const [selectedFile, setSelectedFile] = useState<string | null>(null);
  const [selectedFilePath, setSelectedFilePath] = useState<string[]>([""]);
  const [expandedFolders, setExpandedFolders] = useState<Set<string>>(
    new Set(["root"])
  );
  const [isFileTreeVisible, setIsFileTreeVisible] = useState(true);
  const [lineNumbers, setLineNumbers] = useState<number[]>([]);
  const codeRef = useRef<HTMLPreElement>(null);

  const findFileContent = (
    node: FileSystemNode,
    targetName: string
  ): string | null => {
    if (node.type === "file" && node.name === targetName) {
      return node.content || "";
    }
    if (node.children) {
      for (const child of node.children) {
        const content = findFileContent(child, targetName);
        if (content !== null) return content;
      }
    }
    return null;
  };

  useEffect(() => {
    if (selectedFile) {
      const content = findFileContent(files, selectedFile);
      if (content) {
        const lines = content.split("\n");
        setLineNumbers(Array.from({ length: lines.length }, (_, i) => i + 1));
      }
    }
  }, [selectedFile, files]);

  const toggleFolder = (path: string) => {
    const newExpanded = new Set(expandedFolders);
    if (newExpanded.has(path)) {
      newExpanded.delete(path);
    } else {
      newExpanded.add(path);
    }
    setExpandedFolders(newExpanded);
  };

  const findFilePath = (
    node: FileSystemNode,
    targetName: string,
    currentPath: string[] = []
  ): string[] | null => {
    if (node.type === "file" && node.name === targetName) {
      return [...currentPath, node.name];
    }
    if (node.children) {
      for (const child of node.children) {
        const path = findFilePath(
          child,
          targetName,
          [...currentPath, node.name === "root" ? "" : node.name].filter(
            Boolean
          )
        );
        if (path) return path;
      }
    }
    return null;
  };

  const copyToClipboard = async () => {
    if (!selectedFile) {
      toast.error("No file selected!");
      return;
    }
    const content = findFileContent(files, selectedFile);
    if (content) {
      await navigator.clipboard.writeText(content);
      toast.success("Code copied to clipboard!");
    }
  };

  const downloadFile = () => {
    if (!selectedFile) {
      toast.error("No file selected!");
      return;
    }
    const content = findFileContent(files, selectedFile);
    if (content) {
      const blob = new Blob([content], { type: "text/plain" });
      const url = URL.createObjectURL(blob);
      const a = document.createElement("a");
      a.href = url;
      a.download = selectedFile;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      URL.revokeObjectURL(url);
      toast.success("File downloaded successfully!");
    }
  };

  const selectFile = (fileName: string) => {
    setSelectedFile(fileName);
    const path = findFilePath(files, fileName);
    if (path) {
      setSelectedFilePath(path);
    }
  };

  const renderFileTree = (
    node: FileSystemNode,
    path = "",
    level = 0
  ): React.ReactNode => {
    // If this is the root node, directly render its children instead
    if (node.name === "root" && node.children) {
      return (
        <>{node.children.map((child) => renderFileTree(child, "", level))}</>
      );
    }

    const currentPath = path ? `${path}/${node.name}` : node.name;
    const isExpanded = expandedFolders.has(currentPath);

    return (
      <div key={currentPath} style={{ paddingLeft: level === 0 ? 0 : 12 }}>
        <div
          className={`flex items-center py-1 my-[3px] px-[8px] rounded-lg hover:bg-[#1d1d1d] cursor-pointer ${
            selectedFile === node.name ? "bg-[#1d1d1d]" : ""
          }`}
          onClick={() => {
            if (node.type === "folder") {
              toggleFolder(currentPath);
            } else {
              selectFile(node.name);
            }
          }}
        >
          {node.type === "folder" ? (
            <>
              {isExpanded ? (
                <ChevronDown className="w-4 h-4 mr-[5.4px] text-gray-400" />
              ) : (
                <ChevronRight className="w-4 h-4 mr-[5.4px] text-gray-400" />
              )}
            </>
          ) : (
            <FileIcon className="w-4 h-4 mr-[5.7px] text-blue-400" />
          )}
          <span className="text-sm font-normal">{node.name}</span>
        </div>
        {node.type === "folder" && isExpanded && node.children && (
          <div>
            {node.children.map((child) =>
              renderFileTree(child, currentPath, level + 1)
            )}
          </div>
        )}
      </div>
    );
  };

  return (
    <div className="h-screen flex bg-zinc-950 text-zinc-100">
      {/* Sidebar */}
      {isFileTreeVisible && (
        <div className="w-64 border-r border-zinc-800 flex flex-col overflow-hidden relative px-2 py-1">
          <div className="flex-1 overflow-auto py-2">
            {renderFileTree(files)}
          </div>
        </div>
      )}

      {/* Main Content */}
      <div className="flex-1 overflow-hidden flex flex-col">
        {/* Top Bar with File Path */}
        <div className="flex items-center px-4 py-2 border-b border-zinc-800 text-sm text-gray-400 min-h-[48px]">
          {selectedFilePath.map((segment, index) => (
            <div key={index} className="flex items-center">
              {index > 0 && (
                <ChevronRight className="w-3 h-3 mx-1 text-gray-600" />
              )}
              <span>{segment}</span>
            </div>
          ))}
          <div className="ml-auto flex gap-2">
            <button
              onClick={copyToClipboard}
              className="p-[6px] rounded hover:bg-zinc-800"
              title="Copy code"
            >
              <Copy className="w-4 h-4" />
            </button>
            <button
              onClick={downloadFile}
              className="p-[6px] rounded hover:bg-zinc-800"
              title="Download file"
            >
              <Download className="w-4 h-4" />
            </button>
          </div>
        </div>

        {/* Code Content */}
        <div className="flex-1 overflow-auto bg-zinc-950">
          {selectedFile ? (
            <div className="flex min-h-[100%] h-fit">
              {/* Line Numbers */}
              <div className="p-4 text-xs font-mono leading-5 text-gray-500 select-none border-r border-zinc-800 bg-zinc-900 top-0 h-full min-h-screen">
                {lineNumbers.map((num) => (
                  <div key={num}>{num}</div>
                ))}
              </div>
              {/* Code */}
              <pre
                ref={codeRef}
                className="p-4 text-xs font-mono leading-5 flex-1"
                dangerouslySetInnerHTML={{
                  __html: findFileContent(files, selectedFile) as string,
                }}
              />
            </div>
          ) : (
            <div className="flex h-screen">
              <div className="p-4 text-xs font-mono leading-5 text-gray-500 select-none border-r border-zinc-800 bg-zinc-900 h-full top-0">
                1
              </div>
              <pre className="p-4 text-xs font-mono leading-5 flex-1">{`// Select a file to view its contents`}</pre>
            </div>
          )}
        </div>

        {/* Toggle Button */}
        <button
          className="fixed bottom-4 left-4 w-8 h-8 flex items-center justify-center bg-zinc-900 hover:bg-zinc-800 rounded-sm shadow-lg z-50 border-2 border-white/[0.06]"
          onClick={() => setIsFileTreeVisible(!isFileTreeVisible)}
        >
          {isFileTreeVisible ? (
            <svg
              width="15"
              height="15"
              viewBox="0 0 16 16"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M4.66675 8H14"
                stroke="currentcolor"
                strokeWidth="1.5"
                strokeLinecap="round"
                strokeLinejoin="round"
              ></path>
              <path
                d="M8.66675 12L4.66675 8L8.66675 4"
                stroke="currentcolor"
                strokeWidth="1.5"
                strokeLinecap="round"
                strokeLinejoin="round"
              ></path>
              <path
                d="M2 3.33203V12.6654"
                stroke="currentcolor"
                strokeWidth="1.5"
                strokeLinecap="round"
                strokeLinejoin="round"
              ></path>
            </svg>
          ) : (
            <svg
              width="15"
              height="15"
              viewBox="0 0 16 16"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M11.3333 8H2"
                stroke="currentcolor"
                strokeWidth="1.5"
                strokeLinecap="round"
                strokeLinejoin="round"
              ></path>
              <path
                d="M7.33325 12L11.3333 8L7.33325 4"
                stroke="currentcolor"
                strokeWidth="1.5"
                strokeLinecap="round"
                strokeLinejoin="round"
              ></path>
              <path
                d="M14 3.33203V12.6654"
                stroke="currentcolor"
                strokeWidth="1.5"
                strokeLinecap="round"
                strokeLinejoin="round"
              ></path>
            </svg>
          )}
        </button>
      </div>
    </div>
  );
}
